@media (max-width: 1366px) {
  body {
    font-size: 16px;
    line-height: 22px;
  }

  .header {
    background: rgba(0, 0, 0, .5);
    padding: 25px 0;
    top: 0;
  }

  .top__title {
    font-size: 75px;
    line-height: 90px;
    margin-bottom: 20px;
  }

  .top__inner img {
    width: 80%;
  }

  .top__text {
    margin: 15px 0 25px;
  }

  .title {
    padding-top: 90px;
  }

  .products__slider-box {
    padding-top: 0;
  }

  .products__slider-text {
    bottom: auto;
    left: auto;
    top: -105px;
    right: 0;
  }

  .benefits {
    .title {
      padding-top: 10px;
    }
  }

  .benefits__item {
    margin-bottom: 80px;
  }

  .specification__box-2 {
    margin-top: 115px;
  }

  .specification__box-4 {
    margin-top: 115px;
  }

  .specification__item--center {
    text-align: center;
    margin-top: -55px;

    img {
      width: 80%;
    }
  }

  .specification {
    .title {
      padding-top: 49px;
    }
  }

  .questions {
    .title {
      padding-top: 40px;
      margin-bottom: 20px;
    }
  }

  .questions__item-title {
    line-height: 22px;
    font-size: 18px;
    padding: 16px 10px 16px 30px;
  }

  .questions__item-text {
    padding: 0 5px 40px 30px;
  }

  .questions__item-title::after {
    width: 60px;
    height: 54px;
    right: -60px;
  }
}

