@media (max-width: 1100px) {

  .menu {
    order: 1;
  }

  .menu__list {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background: rgba(28, 28, 28, .9);
    z-index: -1;
    display: flex;
    padding-top: 90px;
    padding-left: 24px;
    flex-direction: column;
    justify-content: space-around;
    transform: translateY(-1000%);
    transition: transform .5s;
    overflow: auto;

    &--active {
      transform: translateY(0%);
    }
  }

  .menu__list-item+.menu__list-item {
    padding-left: 0;
  }

  .menu__list-phone,
  .menu__list-link {
    display: block;
  }

  .menu__list-item {
    margin-bottom: 35px;

    &:first-child {
      margin-top: auto;
    }
  }

  .menu__list-phone {
    padding-top: 33px;
    position: relative;
    margin-top: auto;
    margin-bottom: auto;
    font-weight: bold;
    font-size: 16px;
    line-height: 18px;

    &::before {
      content: '';
      position: absolute;
      border-top: 1px solid rgba(255, 255, 255, .24);
      top: 0;
      left: -24px;
      right: 0;
    }
  }

  .menu__btn {
    display: block;
    background-color: transparent;
    border: none;
    outline: none;
    padding: 0;
    cursor: pointer;
  }

  .menu__btn::before,
  .menu__btn::after {
    content: "";
    width: 24px;
    height: 2px;
    background-color: #e0e0e0;
    display: block;
    margin-bottom: 4px;
    transition: transform .3s;
  }

  .menu__btn::after {
    margin-bottom: 0;
    margin-top: 3px;
    width: 16px;
    margin-left: auto;
  }


  .menu__btn-line {
    background-color: #e0e0e0;
    display: block;
    width: 24px;
    height: 2px;
    margin-bottom: 3px;
    opacity: 1;
    transition: opacity .6s;
  }

  .menu__btn.menu__btn--active {
    .menu__btn-line {
      opacity: 0;
      transition: opacity 0s;
    }

    &::after {
      width: 24px;
      transform: translateY(-6px) rotate(-135deg);
    }

    &::before {
      transform: translateY(5px) rotate(135deg);
    }
  }

  .header__phone {
    display: none;
  }

  .header__btn {
    margin-right: 0;
  }

  .products__text {
    padding-right: 15px;
  }

  .products__slider-box {
    padding-right: 20px;
  }

  .benefits__item:first-child::before {
    display: none;
  }

  .benefits__item:nth-child(2)::before {
    display: none;
  }

  .benefits__item:nth-child(3)::before {
    display: none;
  }

  .benefits__items {
    display: block;
  }

  .benefits__item {
    width: 100%;
    margin-bottom: 40px;
  }

  .benefits__item:nth-child(2) {
    margin-left: 200px;
    transform: translateY(0);
  }

  .benefits__item:nth-child(4) {
    margin-left: 200px;
    transform: translateY(0);
  }

  .benefits__info {
    width: 35%;
  }
}


